@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,300;0,400;0,500;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,300;0,400;0,500;1,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    list-style: none;
}
.footer{
     position: relative;
     bottom: 0;
     width: 100%;
     height: 300px;
     background-image: linear-gradient(180deg,rgba(60,207,145,.58),rgba(60,207,145,.58)),linear-gradient(
        90deg,rgba(18,20,29,.5),hsla(0,0%,100%,0)),url(/react-website/static/media/footer.b6e7a24e.jpg);
     background-size: contain cover;
     background-repeat: no-repeat; 
 }
.footer .container{
    display: flex;
    text-align: center;
    justify-content: center;
    padding: 35px;
}
.icons{
    color:rgb(6, 46, 46);
    display: flex;
    cursor: pointer;
    padding-right: 20px;
}
.icons a{
    margin: 10px 50px;
    border-radius: 60%;
}
.icons a:hover{
   color: #8d2005;
}
.footer .container h1{
    color: rgb(6, 46, 46);
    padding-right:10px;
    display: block;
    font-size: 2rem;
    margin: 20px 0px;
    border:none;
}
.footer .container h1:hover{
    border-bottom: #fff;
}

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.grid{
   display: grid;
   grid-template-columns: repeat(1fr ,4);
   grid-gap: 10px;
   gap: 10px;
}
.navbar{
    position: fixed;
    top: 0;
    width: 100%;
    height: 80px;
    background-color: #4bd2b2;
    padding: 30px 40px;
    overflow: none;
    display: flex;
    align-items: center;
    z-index: 100;
}
.nav-logo{
   color: #fff;
   font-size: 40px;
   cursor: pointer;
   padding-right: 60px;
   margin-bottom: 12px;
}
.navmenu{
    display: flex;
    z-index: 90;
    justify-content: space-between;
    vertical-align: baseline;
}
.navmenu .navlink{
    font-family: 'Montserrat',sans-serif;
    font-size: 23px;
    font-weight: 600;
    color: #73737b;
    margin: 30px;
    cursor: pointer;
    transition: all .4s;
}
.navmenu .navlink:hover{
    border-bottom: solid #fff;
}
.btn{
    display: inline-block;
    border: 3px solid #222;
    background-color:#0f1124;
    border-radius: 7px;
    height: 55px;
    color: #4bd2b2;
    text-decoration: none;
    text-align: center;
    font-size: 20px;
    width: 170px;
    justify-self: flex-end;
    margin: 20px; 
    margin-right: 0px;
    margin-left: 70px;
}
.btn:hover{
    border: none;
    color: #fff;
    background: #ee0979;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #ff6a00, #ee0979); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
 
@media (max-width : 1100px) {
    .navmenu .navlink{
        display: none;
    }
    .btn{
        margin-top: 8px;
    }
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.carousel{
    font-family: 'Monserrat',sans-serif;
   align-items: center;
    justify-content: center;
    height: 100vh;
    border: 1px solid #f0f0f0;
  }
.carousel{
    text-align: center;
    padding: 10px;
}
.carousel img{
    width: 350px;
    height: 500px;
    /* margin: 10px; */
    margin-bottom: 0;
    object-fit: contain;
}
.carousel h1{
    font-size: 2.4rem;
    color: #35584e;
    font-family: 'Monserrat', sans-serif;
    margin-top: 50px;
    margin-left: 64px;
    margin-bottom: 7px;
}
.carousel img{
    display: block;
    margin: 3px;
}
.carousel img:hover{
   opacity: 0.6;
}

@media (max-width: 1000px) {

    .carousel img{
        width: 200px;
        height: 500px;
    }
    .carousel-container{
        margin-top: 20px;
    }
}
*{
    box-sizing: border-box;
    line-height: 1.7em;
}
.main-container{
    display: block;
    max-height: 1200px;
    min-height: 600px;
    height: 100%;
    padding-top: 100px;
    background: #fff;  /* fallback for old browsers */
    /* background: -webkit-linear-gradient(to right, #243B55, #141E30);  
    background: linear-gradient(to right, #243B55, #141E30);
 */ } 
.container{
    display: flex;
    margin: 30px;
}
.container .text{
    margin: 50px;
}
.container h1{
    color:#0f101f;
    font-size: 3.1rem;
    margin-bottom: 12px;
}
.container p{
    color: black;
    font-size: 1.2rem;
}
.container .btn{
    color: #fff;
    margin-left: 0;
}

/*stats*/
.stats{
  position: relative;
  background-color: #c3e2db;
  padding: 12px;
  text-align: center;
  max-height: 900px;
  min-height: 300px;
}
.stats-container img{
    background-color: black;
    border-radius:50% ;
    padding: 5px;
}
.stats-container{
    display: flex;
}
.stats-data{
    margin: 50px;
    background-color: none;
    width: 400px;
    height: 200px;
    border-radius: 8px;
    border:3px solid #60aa95;
    padding: 30px;
}
.stats-data:hover{
    border:none;
    background: #ee0979;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #ff6a00, #ee0979); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.stats h1{
    color:#176956;
    font-size: 2.4rem;
    margin-bottom: 12px;
}
/* Companies */
.companies{
    display: block;
    position: relative;
    text-align: center;
    padding: 10px;
    margin: 30px;
    margin-bottom:50px;
}
.companies h1{
    color:#222;
    font-size: 2rem;
    margin-bottom: 26px;
    line-height: 48px;
}
.companies .container{
   display: table;
}
.companies img{
    width: 200px;
    margin: 7px;
}
.container{
    padding: 50px;
}
.container .text1{
    padding: 50px;
}
.container .text1 h1{
    font-size: 3rem;
    font-weight: 600;
    line-height: 3.2rem;
    margin-bottom: 12px;
}
.container .text1 p{
    font-size: 1.3rem;
    line-height: 2.6rem;
    margin-top:12px;
}

@media (max-width : 1000px) {
    .main-container{
        padding-top: 70px;
    }
    .container h1{
        font-size: 2rem;
    }
    .container p{
        margin-bottom: 10px;
    }
    .container{
        display: block;
        text-align: center;
    }
    .container img{
        width: 500px;
    }
    .stats-container{
        display: block;
    }
    .stats h1{
       font-size: 2.2rem;
       justify-items: center;
    }
    .container .text1 h1{
        font-size: 2.3rem;
    }
    .container .text1 p{
        font-size: 1.3em;
        line-height: 2.6rem;
        margin-top:1px;
    }
    .details img{
       width: 400px;
       height: 400px;
       justify-self: center;
    }
}
